import { Link } from 'react-router-dom';
import '../styles/FrontPage.css';

const FrontPage = () => {
  return (
    <div>
      <div className="textAndGraphics">
        <div>
          <div className="mobileGraphicsContainer">
            <img
              className="frontPageImage"
              src={require('../images/img.jpg')}
              alt="Kuva minusta."
            />

            <div className="circleContainer">
              <div className="circle" />

              <div className="circle" />

              <div className="circle" />

              <div className="circle" />

              <div className="circle" />
            </div>
          </div>

          <h2>Hei!</h2>

          <p>
            Olen Ville Kuusela, 22-vuotias tietojenksittelyn opiskelija
            Jyvskylst. Erikoisalaani on frontend, mutta hallitsen koko Full
            Stackista vhintn perusteet.
          </p>

          <p>
            Tll sivustolla voit tutustua minuun itseeni, sek muutamiin
            opintojeni aikana luomiini <Link to="/projektit">projekteihin</Link>
            . Lydt mys <Link to="/yhteystiedot">yhteystietoni</Link>, jos
            haluat ottaa yhteytt.
          </p>

          <h3>Opinnot</h3>

          <p>
            Aloitin tietojenksittelyn opiskelun Jyvskyln
            ammattikorkeakoulussa syksyll 2020. Sit edeltv kokemukseni
            ohjelmoinnista oli hyvin vhist, mutta lyhyess ajassa olen
            oppinut valtavasti.
          </p>

          <p>
            Tst kertoo muun muassa erinomaiset arvosanani, sek
            opintomenestyksestni minulle mynnetty stipendi. Stipendin
            myntmisperusteena oli lukuvuoden paras keskiarvo alalla.
          </p>

          <p>Tavoitteenani on valmistua joulukuussa 2023.</p>

          <p>
            Ennen JAMK:issa aloittamista valmistuin Schildtin lukiosta erittin
            hyvin paperein.
          </p>

          <h3>Taidot</h3>

          <p>
            Ominta alaani on frontend-ohjelmointi. Minulla on kokemusta Reactin,
            Angularin sek Svelten kytst. HTML, CSS ja JavaScript/TypeScript
            sujuvat minulta erinomaisen hyvin ottaen huomioon kuinka lyhyen
            aikaa olen vasta opiskellut niit.
          </p>

          <p>
            Mys backendin puolelta osaan monet asiat vhintn perustasolla.
          </p>

          <p>
            Lisksi olen nopea oppimaan uutta, mist todisteena on
            opintomenestykseni. Tulen hyvin toimeen kaikenlaisten ihmisten
            kanssa.
          </p>
        </div>

        <div className="wideGraphicsContainer">
          <div className="graphics">
            <img
              className="frontPageImage wideScreenImage"
              src={require('../images/img.jpg')}
              alt="Kuva minusta."
            />

            <div className="circleContainer wideScreenCircles">
              <div className="circle" />

              <div className="circle" />

              <div className="circle" />

              <div className="circle" />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FrontPage;
